
  .blocks{
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    margin-bottom: 3rem;
  }


  @media(max-width: 1200px) {.blocks{grid-template-columns: 1fr 1fr}}
  @media(max-width: 560px) {.blocks{grid-template-columns: 1fr}}
