$col1: #aab;


.block{
    width: 300px;
    height: auto;
    background: #222229;
    margin: 1rem;
    text-decoration: none;
    box-sizing: border-box;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: 0.2s;
    &:hover{
        background: #272733;
    }
    &:hover .temp img{
        opacity: 0.5;
    }
    .name{
        color: #889;
    }
  }
  
  .temp{
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1rem;
      box-sizing: border-box;
      align-items: center;
      h1{
        font-size: 42px;
        color: $col1;
      }
      img{
          opacity: 0.3;
          width: 100%;
          transition: 0.2s;
      }
  }
  .info_weather{
      margin-top: 1rem;
      p{
        color: $col1;
        display: flex;
        justify-content: space-between;
        font-size: 18px;
        span{
          color: $col1;
        }
      }
  }
  

  @media(max-width: 770px) {
    .block{
        width: 220px;
        
      .name{
        font-size: 24px;
      }
      .temp{
        h1{
          font-size: 32px;
        }
      }
    }
  }

  @media(max-width: 560px) {
    .block{
        width: 300px;
    }
  }
  @media(max-width: 375px) {
    .block{
        width: 220px;
    }
  }
  @media(max-width: 300px) {
    .block{
        width: 200px;
    }
  }
  @media(max-width: 280px) {
    .block{
        width: 200px;
        margin: 1rem 0;
    }
  }
